<template>
  <div ref="wrapper" style="position: relative">
    <div ref="listWrapper">
      <slot></slot>
      <div class="hs-pullup-wrapper" v-if="pullUpLoad">
        <div class="before-trigger" v-if="!isPullUpLoad">
          <span>{{pullUpDirty ? $t('base.PullUpLoadMore') : $t('base.AllLoaded')}}</span>
        </div>
        <div class="after-trigger" v-else>
          <hs-loading></hs-loading>
        </div>
      </div>
    </div>
    <div ref="pulldown" v-if="pullDownRefresh">
      <div class="hs-pulldown-wrapper" :style="pullDownStyle">
        <div class="before-trigger" v-show="beforePullDown">
          <hs-bubble :y="bubbleY"></hs-bubble>
        </div>
        <div class="after-trigger" v-show="!beforePullDown">
          <div v-show="isPullingDown" class="loading">
            <hs-loading></hs-loading>
          </div>
          <div v-show="!isPullingDown">
            <p class="refreshTxt">{{$t('base.RefreshSucceed')}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
/*
 * better-scrolltableviewscroll
 * 1.props.options
 * pullDownRefresh: {
 *   threshold: 50 // 
 * }
 * @pulling-downthis.$refs.[refName].forceUpdate()
 * 2., , props.options
 * pullUpLoad: {
 *   threshold: 50
 * }
 * @pulling-upthis.$refs.[refName].forceUpdate()
 * 3.dataprops.data : :data="listdata"
 * :
 * <hs-scroll
 *   ref="tableView"
 *   :data="list"
 *   class="datalist"
 *   :options="tableViewOptions"
 *   @pulling-down="_getData"
 *   @pulling-up="_getNextData">
 *   <ul>
 *     <li class="item" v-for="(item, index) in list" :key="index">{{index}}</li>
 *   </ul>
 * </hs-scroll>
 * tableViewOptions: {
 *   pullDownRefresh: {
 *     threshold: 50
 *   },
 *   pullUpLoad: {
 *     threshold: 50
 *   }
 * }
*/
import BScroll from 'better-scroll'
import HsBubble from 'base/hs-bubble/hs-bubble'
import HsLoading from 'base/hs-loading/hs-loading'
import {getRect} from 'common/js/dom'
const DEFAULT_OPTIONS = {
  observeDOM: true,
  click: true,
  tap: true,
  probeType: 3,
  scrollbar: false,
  pullDownRefresh: false,
  pullUpLoad: false
}
const DIRECTION_H = 'horizontal' // 
const DIRECTION_V = 'vertical' // 

export default {
  props: {
    data: {
      type: Array,
      default() {
        return []
      }
    },
    options: {
      type: Object,
      default: function() {
        return {
          pullDownRefresh: false,
          pullUpLoad: false
        }
      }
    },
    direction: {
      type: String,
      default: DIRECTION_V
    },
    isListenScroll: {
      type: Boolean,
      default: false
    }
  },
  components: {
    HsBubble,
    HsLoading
  },
  activated() {
    setTimeout(() => {
      this.forceUpdate(true)
    }, 20)
  },
  mounted() {
    this.$nextTick(() => {
      this._initScroll()
    })
  },
  beforeDestroy() {
    this.destroy()
  },
  data() {
    return {
      beforePullDown: true,
      isPullingDown: false,
      isPullUpLoad: false,
      pullUpDirty: true,
      pullDownStyle: '',
      pullDownHeight: 0,
      pullDownStop: 0,
      bubbleY: 0
    }
  },
  computed: {
    pullDownRefresh() {
      /** scrolloptions.pullDownRefreshpullDownRefresh */
      let res = this.options.pullDownRefresh
      if (!res) {
        return res
      }
      if (res === true) {
        res = {}
      }
      return Object.assign({stop: this.pullDownStop}, res)
    },
    pullUpLoad() {
      return this.options.pullUpLoad
    }
  },
  watch: {
    data() {
      setTimeout(() => {
        this.forceUpdate(true)
      }, 20)
    },
    pullDownRefresh: {
      handler(newVal, oldVal) {
        /** , false => true, pullingDownscroll */
        if (newVal) {
          this.scroll.openPullDown(newVal)
          if (!oldVal) {
            this._onPullDownRefresh()
            this._calculateMinHeight()
          }
        }
        /** true => false, ,pullingDownscroll */
        if (!newVal && oldVal) {
          this.scroll.closePullDown()
          this._offPullDownRefresh()
          this._calculateMinHeight()
        }
      }
    },
    pullUpLoad: {
      handler(newVal, oldVal) {
        if (!this.scroll) return
        if (newVal) {
          this.scroll.openPullUp(newVal)
          if (!oldVal) {
            this._onPullUpLoad()
            this._calculateMinHeight()
          }
        }
        if (!newVal && oldVal) {
          this.scroll.closePullUp()
          this._offPullUpLoad()
          this._calculateMinHeight()
        }
      },
      deep: true
    }
  },
  methods: {
    /** , scroll,  = beforePullDown */
    _initScroll() {
      if (!this.$refs.wrapper) return
      this._calculateMinHeight()
      let options = Object.assign(DEFAULT_OPTIONS, this.options, {
        scrollY: this.direction === DIRECTION_V,
        scrollX: this.direction === DIRECTION_H
      })
      // console.log('options: ', options)
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.wrapper, options)
      }
      if (this.isListenScroll) {
        this.scroll.on('scrollStart', () => {
          this.$emit('scrollStart')
        })
        this.scroll.on('scroll', (pos) => {
          this.$emit('scroll', pos)
        })
        this.scroll.on('touchEnd', (pos) => {
          this.$emit('touchEnd', pos)
        })
      }
      if (this.pullDownRefresh) {
        this._getPullDownEleHeight()
        this._onPullDownRefresh()
      }
      if (this.pullUpLoad) {
        this._onPullUpLoad()
      }
    },
    _getPullDownEleHeight() {
      /** 2-1, loading, pullDownHeight */
      const pulldown = this.$refs.pulldown.firstChild
      this.pullDownHeight = getRect(pulldown).height
      this.beforePullDown = false
      this.isPullingDown = true
      this.$nextTick(() => {
        /** 2-2, loading, pullDownStop */
        this.pullDownStop = getRect(pulldown).height
        this.beforePullDown = true
        this.isPullingDown = false
      })
    },
    _onPullDownRefresh() {
      this.scroll.on('pullingDown', this._pullDownHandle)
      this.scroll.on('scroll', this._pullDownScrollHandle)
    },
    _offPullDownRefresh() {
      this.scroll.off('pullingDown', this._pullDownHandle)
      this.scroll.off('scroll', this._pullDownScrollHandle)
    },
    _pullDownHandle() {
      /** , ,  */
      if (this.resetPullDownTimer) {
        clearTimeout(this.resetPullDownTimer)
      }
      /** 4-2  */
      this.beforePullDown = false
      this.isPullingDown = true
      /** 4-4 $emit('pulling-down')scroll */
      this.$emit('pulling-down')
    },
    _pullDownScrollHandle(pos) {
      if (this.beforePullDown) {
        /** 4-1  */
        this.bubbleY = Math.max(0, pos.y - this.pullDownHeight)
        this.pullDownStyle = `top:${Math.min(pos.y - this.pullDownHeight, 0)}px`
      } else {
        /** 4-3 / */
        this.bubbleY = 0
        this.pullDownStyle = `top:${Math.min(pos.y - this.pullDownStop, 0)}px`
      }
    },
    /** 4-4, $emit('pulling-down') */
    forceUpdate(dirty = false) {
      if (this.pullDownRefresh && this.isPullingDown) {
        this.isPullingDown = false // 
        /** ,  */
        this._reboundPullDown(() => {
          this._afterPullDown(dirty)
        })
      } else if (this.pullUpLoad && this.isPullUpLoad) {
        this.isPullUpLoad = false
        this.scroll.finishPullUp()
        this.pullUpDirty = dirty
        dirty && this.refresh()
      } else {
        dirty && this.refresh()
      }
    },
    refresh() {
      setTimeout(() => {
        this._calculateMinHeight()
        this.scroll && this.scroll.refresh()
      }, 300)
    },
    _reboundPullDown(next) {
      /** scroll,  */
      setTimeout(() => {
        this.scroll && this.scroll.finishPullDown()
        this.scroll && next()
      }, 600)
    },
    _afterPullDown(dirty) {
      /** , , scroll */
      this.resetPullDownTimer = setTimeout(() => {
        this.pullDownStyle = `top: -${this.pullDownHeight}px`
        this.beforePullDown = true // 
        dirty && this.refresh()
      }, this.scroll.options.bounceTime)
    },
    /** better-scroll, listWrapperwrapper */
    _calculateMinHeight() {
      if (this.$refs.listWrapper) {
        this.$refs.listWrapper.style.minHeight = this.pullDownRefresh || this.pullUpLoad ? `${getRect(this.$refs.wrapper).height + 1}px` : 0
      }
    },
    _onPullUpLoad() {
      this.scroll.on('pullingUp', this._pullUpHandle)
    },
    _offPullUpLoad() {
      this.scroll.off('pullingUp', this._pullUpHandle)
    },
    _pullUpHandle() {
      this.isPullUpLoad = true
      this.$emit('pulling-up')
    },
    /**  */
    destroy() {
      this.scroll && this.scroll.destroy()
      this.scroll = null
    },
    scrollTo() {
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    scrollToElement() {
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-pulldown-wrapper
  @extend .flex-center
  position absolute
  width 100%
  left 0
  transition all
  .before-trigger
    height 54px
    line-height 0
    padding-top 6px
    pointer-events none
    >canvas
      pointer-events none
  .after-trigger
    .loading
      padding 8px 0
    .refreshTxt
      font-size 0.8rem
      padding 12px 0
.hs-pullup-wrapper
  @extend .flex-center
  width 100%
  .before-trigger
    padding 12px 0
    min-height 1em
  .after-trigger
    padding 9px 0
</style>



